
body
{
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
  color: #303030;
}
.table-layout
{
  display: table;
  table-layout: fixed;
  width: 100%;
}
.table-layout > *
{
  display: table-row;
}
.table-layout > * > *
{
  display: table-cell;
}



